import { Builder } from '../../Builder'

export interface LabeledPrice {
    label: string
    amount: number
}

export interface InputInvoiceMessageContentPayload {
    title: string
    description: string
    payload: string
    provider_token?: string
    currency: string
    prices: LabeledPrice[]
    max_tip_amount?: number
    suggested_tip_amounts?: number[]
    provider_data?: string
    photo_url?: string
    photo_size?: number
    photo_width?: number
    photo_height?: number
    need_name?: boolean
    need_phone_number?: boolean
    need_email?: boolean
    need_shipping_address?: boolean
    send_phone_number_to_provider?: boolean
    send_email_to_provider?: boolean
    is_flexible?: boolean
}

export class InputInvoiceMessageContent extends Builder<InputInvoiceMessageContentPayload> {

    public constructor() {
        super({ title: '', description: '', payload: '', currency: '', prices: [] })
    }

    public title(value: string): this {
        this.packet.title = value
        return this
    }

    public description(value: string): this {
        this.packet.description = value
        return this
    }

    public payload(value: string): this {
        this.packet.payload = value
        return this
    }

    public providerToken(value?: string): this {
        this.packet.provider_token = value
        return this
    }

    public currency(value: string): this {
        this.packet.currency = value
        return this
    }

    public prices(value: LabeledPrice[]): this {
        this.packet.prices = value
        return this
    }

    public maxTipAmount(value?: number): this {
        this.packet.max_tip_amount = value
        return this
    }

    public suggestedTipAmounts(value?: number[]): this {
        this.packet.suggested_tip_amounts = value
        return this
    }

    public providerData(value?: string): this {
        this.packet.provider_data = value
        return this
    }

    public photoUrl(value?: string): this {
        this.packet.photo_url = value
        return this
    }

    public photoSize(value?: number): this {
        this.packet.photo_size = value
        return this
    }

    public photoWidth(value?: number): this {
        this.packet.photo_width = value
        return this
    }

    public photoHeight(value?: number): this {
        this.packet.photo_height = value
        return this
    }

    public needName(value?: boolean): this {
        this.packet.need_name = value
        return this
    }

    public needPhoneNumber(value?: boolean): this {
        this.packet.need_phone_number = value
        return this
    }

    public needEmail(value?: boolean): this {
        this.packet.need_email = value
        return this
    }

    public needShippingAddress(value?: boolean): this {
        this.packet.need_shipping_address = value
        return this
    }

    public sendPhoneNumberToProvider(value?: boolean): this {
        this.packet.send_phone_number_to_provider = value
        return this
    }

    public sendEmailToProvider(value?: boolean): this {
        this.packet.send_email_to_provider = value
        return this
    }

    public flexible(value?: boolean): this {
        this.packet.is_flexible = value
        return this
    }

}
